import React from "react";
import {Link} from 'react-router-dom'
import posed from 'react-pose';
import profilePicture from '../profilePic.png'
import '../index.css'
import '../index.js'

export const Container = posed.div({
  enter: { staggerChildren: 50 },
  exit: { staggerChildren: 20, staggerDirection: -1}
});

export const Div = posed.div({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const H3 = posed.h3({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const Li = posed.li({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const H1 = posed.h1({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const H5 = posed.h5({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const Button = posed.div({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const Image = posed.img({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const P = posed.p({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

function Homepage(props) {
  return (
    <Container>
      <div class="d-flex flex-column align-items-center center">
        { /* profile picture */ }
        <Image src={profilePicture} alt="profilePic"/>
        {/* Figure out a way to center the image in css */ }
        <H3 class="text-center bg-dark text-white font-weight-normal mt-4"> {props.name} </H3>
        <H5 class="text-center text-white font-weight-normal"> {props.occupation} </H5>
        { /* add buttons that connect to the router*/ }
        <div class="buttons">
          <Link to='/aboutme'>
            <Button type="button" class="btn btn-dark">About Me</Button>
          </Link>
          <Link to='/contact'>
            <Button type="button" class="btn btn-dark">Contact</Button>
          </Link>
          <Link to='/projects'>
            <Button type="button" class="btn btn-dark">Projects</Button>
          </Link>
        </div>
      </div>
    </Container>
  )
}

export default Homepage;
